import React, { useEffect, useState } from 'react';

// material-ui
import { Grid, Typography, Chip } from '@material-ui/core';
import AddBrokerCard from 'views/dashboard/Default/AddBrokerCard';

import SubCard from 'ui-component/cards/SubCard';

import VerifyAccountCard from 'views/dashboard/Default/VerifyAccountCard';
import { gridSpacing } from 'store/constant';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import configData from 'config';
import { makeStyles, useTheme } from '@material-ui/styles';

//-----------------------|| DEFAULT DASHBOARD ||-----------------------//

const Dashboard = () => {   
    // const dispatcher = useDispatch(); 
    const account = useSelector((state) => state.account);
    // const error = React.useState(false);
    const search = window.location.search;
    const params = new URLSearchParams(search);
    const code = params.get('code');
    const theme = useTheme();
    const [enabled, setEnabled] = useState(null);
    const [accounts, setAccounts] = useState([]);
    let history = useHistory();

    useEffect(() => {
        if (code){
            console.log(code);
            axios.post(configData.API_SERVER+"users/access_key?code="+code+"&url="+encodeURIComponent(window.location.href), {}, {headers: { authorization: `${account.token}` }})
            .then(response => {
                console.log(response);
                window.location = window.location.href.split('?')[0];
            })
        }
      }, []);

    useEffect(() => {
        axios.get(configData.API_SERVER+"users/accounts", {headers: { authorization: `${account.token}` }})
        .then(response => {
            console.log(response);
            setAccounts(response.data);
        })
    }, []);

    useEffect(() => {
        axios.get(configData.API_SERVER+"users/enabled", {headers: { authorization: `${account.token}` }})
        .then(response => {
            setEnabled(response.data.is_enabled);
        })
    }, []);



    return ( <Grid container spacing={gridSpacing}>
        <Grid item xs={12}>
            <Grid container spacing={gridSpacing}>
                { accounts.map((item, index) => (
                        <Grid item xs={12} md={6} lg={4} key={index}>
                            <SubCard 
                                onClick={() => history.push("/utils/accounts/"+item['_id'])}
                                title={
                                    <Grid container>
                                        <Grid item xs={6}>
                                            <Typography variant="h2">
                                                {item.name} 
                                            </Typography>
                                            <Typography variant="h4" >
                                                {item.public_key}
                                            </Typography>
                                        </Grid>
                                        <Grid container item xs={6} justifyContent="right">
                                            <img height={50} width={50} src={item.icon}></img>
                                        </Grid>
                                    </Grid>
                                }
                                sx={{cursor: "pointer"}}>
                                <Grid container>
                                    <Grid container item xs={6}>
                                        <Typography variant="h1" >
                                            {item.info.equity} 
                                        </Typography>
                                    </Grid>
                                    <Grid container item xs={6} justifyContent="right">
                                        {item.info.side ? <Chip label={item.info.change} color="success"/>
                                        : (item.info.side === false) ? <Chip label={item.info.change} color="error"/>
                                        : <></>
                                        }
                                    </Grid>
                                </Grid>
                            </SubCard>
                        </Grid> 
                    ))
                }
                {enabled === true &&
                    <Grid item xs={12} md={6} lg={4}>
                        <AddBrokerCard />
                    </Grid>}
                {enabled === false && 
                    <Grid item xs={12} md={6} lg={4}>
                        <VerifyAccountCard />
                    </Grid>
                }
            </Grid>
        </Grid>
    </Grid>
    );
    
};

export default Dashboard;
