import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';

// material-ui
import { makeStyles, useTheme } from '@material-ui/styles';
// import { Grid, Typography, Button, Modal, Box, Card, Divider } from '@material-ui/core';
import { useSelector } from 'react-redux';
import {
    Grid,
    Box,
    Button,
    Checkbox,
    FormControl,
    FormControlLabel,
    FormHelperText,
    IconButton,
    InputAdornment,
    InputLabel,
    OutlinedInput,
    Stack,
    Typography,
    Modal,
    Card,
    Divider
} from '@material-ui/core';

import { gridSpacing } from 'store/constant';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import SubCard from 'ui-component/cards/SubCard';
import SkeletonEarningCard from 'ui-component/cards/Skeleton/EarningCard';
import configData from 'config';
import axios from 'axios';
import AnimateButton from 'ui-component/extended/AnimateButton';


// style constant
const useStyles = makeStyles((theme) => ({
    card: {
        backgroundColor: theme.palette.secondary.main,
        color: '#fff',
        overflow: 'hidden',
        position: 'relative',
    },
    avatar: {
        ...theme.typography.commonAvatar,
        ...theme.typography.largeAvatar,
        backgroundColor: theme.palette.secondary[800],
        marginTop: '8px'
    },
    avatarRight: {
        ...theme.typography.commonAvatar,
        ...theme.typography.mediumAvatar,
        backgroundColor: theme.palette.secondary.dark,
        color: theme.palette.secondary[200],
        zIndex: 1
    },
    cardHeading: {
        fontSize: '1.5rem',
        fontWeight: 500,
        marginRight: '8px',
        marginTop: '14px',
        marginBottom: '6px'
    },
    subHeading: {
        fontSize: '1rem',
        fontWeight: 500,
        color: theme.palette.secondary[200]
    },
    avatarCircle: {
        cursor: 'pointer',
        ...theme.typography.smallAvatar,
        backgroundColor: theme.palette.secondary[200],
        color: theme.palette.secondary.dark
    },
    circleIcon: {
        transform: 'rotate3d(1, 1, 1, 45deg)'
    },
    menuItem: {
        marginRight: '14px',
        fontSize: '1.25rem'
    },
    cardAction: {
        padding: '10px',
        paddingTop: 0,
        justifyContent: 'center'
    },
    primaryLight: {
        color: theme.palette.primary[200],
        cursor: 'pointer'
    },
    divider: {
        marginTop: '12px',
        marginBottom: '12px'
    },
    avatarSuccess: {
        width: '32px',
        height: '32px',
        borderRadius: '5px',
        backgroundColor: theme.palette.success.light,
        color: theme.palette.success.dark,
        marginLeft: '15px'
    },
    successDark: {
        color: theme.palette.success.dark
    },
    avatarError: {
        width: '32px',
        height: '32px',
        borderRadius: '5px',
        backgroundColor: theme.palette.orange.light,
        color: theme.palette.orange.dark,
        marginLeft: '15px'
    },
    errorDark: {
        color: theme.palette.orange.dark
    },
    loginInput: {
        ...theme.typography.customInput
    }
}));

//===========================|| DASHBOARD DEFAULT - EARNING CARD ||===========================//

const AddBrokerCard = () => {
    const [isLoading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [brokers, setBrokers] = useState([]);
    const [brokerID, setBrokerID] = useState(false);

    const [publics, setPublic] = useState("");
    const [privates, setPrivate] = useState("");
    const [error, setError] = useState(null);

    const theme = useTheme();
    const classes = useStyles();
    const account = useSelector((state) => state.account);
    
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 600,
        maxWidth: "100%",
        bgcolor: 'background.paper',
      };
      
    const handleClose = () => {
        setBrokerID(false);
        setOpen(false);
    }

    const handleChangePublic = (text) => {
        setPublic(text.target.value);
    }

    const handleChangePrivate = (text) => {
        setPrivate(text.target.value);
    }

    const setSelf = () => {
        axios.post(configData.API_SERVER + 'users/accounts', {
            public_key: publics,
            private_key: privates,
            broker_id: brokerID["_id"]
        }, { headers: { Authorization: `${account.token}` } })
        .then(() => handleClose())
        .then(() => window.location.reload())
        .catch(function (error) {
            setError(error.response.data.msg);
        });
        return false;
        // setError
    }
    
    useEffect(() => {
        const url = configData.API_SERVER+"brokers";
        axios.get(url, { headers: { Authorization: `${account.token}` } })
          .then((response) => setBrokers(response.data))
    }, []);

    return (
        <React.Fragment>
            {isLoading ? (
                <SkeletonEarningCard />
            ) : (
                <MainCard border={false} className={classes.card} contentClass={classes.content}>
                    <Grid container direction="column">
                        <Grid item>
                            <Button
                                className={classes.cardHeading}
                                disableElevation
                                fullWidth
                                size="large"
                                type="submit"
                                variant="contained"
                                color="secondary"
                                onClick={() => { 
                                    setOpen(true);
                                    // window.open("https://app.alpaca.markets/oauth/authorize?response_type=code&client_id=a6fa01dfdd7fa6f4ce4997e10ecad9d1&redirect_uri="+encodeURIComponent(window.location.href)+"&state=1&scope=account:write%20trading");
                                }}
                            >
                                + Connect an account
                            </Button>
                            {/* <Typography className={classes.subHeading}></Typography> */}
                            <Modal
                            open={open}
                            onClose={handleClose}
                            sx={{margin: 2}}
                            aria-labelledby="modal-modal-title"
                            aria-describedby="modal-modal-description"
                            >
                                <MainCard sx={style}>
                                    {brokerID === false ?
                                    <div>
                                        <Typography variant="h2" id="modal-modal-title">
                                        Connect a new broker account from one of our available integrations!
                                        </Typography>
                                        <Typography variant="subtitle1" id="modal-modal-description">
                                            We're always expanding what we offer, check back later for new integrations!
                                        </Typography>
                                    </div> :
                                    <div>
                                        <Grid container direction="row" alignItems="center">
                                            {/* <img height={50} width={50} src={brokerID.icon}></img> */}
                                            <Typography variant="h2" id="modal-modal-title">
                                                Connecting to {brokerID.name}
                                            </Typography>
                                        </Grid>
                                        <Typography variant="subtitle1" id="modal-modal-description">
                                            All we need are your API keys, but don't worry they'll be save with us!
                                        </Typography>
                                        {/* <Typography variant="h2" id="modal-modal-title">
                                        Connecting to {brokerID.name}
                                        </Typography>
                                        <Typography variant="subtitle1" id="modal-modal-description">
                                            Good choice! All we need are your API keys, but don't worry they'll be save with us!
                                        </Typography> */}
                                    </div>
                                    }
                                    <Divider className={classes.divider} />
                                    <Grid container spacing={gridSpacing} direction="row" justifyContent="space-evenly" alignItems="center">
                                    {brokerID === false ? 
                                            brokers.map(item => (
                                                <Grid item key={item.name} xs={12} md={6} onClick={() => setBrokerID(item)}>
                                                    <SubCard
                                                    sx={{cursor: "pointer"}}>
                                                        <Grid container direction="row" justifyContent="space-evenly" alignItems="center">
                                                            <Grid item xs={6}>
                                                                <Typography variant="h3" >
                                                                    {item.name}
                                                                </Typography>
                                                            </Grid>
                                                            <Grid container item xs={6} justifyContent="right">
                                                                <img height={50} width={50} src={item.icon}></img>
                                                            </Grid>
                                                        </Grid>
                                                    </SubCard>
                                                </Grid>
                                            )
                                    ) : 
                                    <Grid item>
                                    <form>
                                        {/* <Grid container direction="row" justifyContent="space-evenly" alignItems="center">
                                            <Grid item xs={6}>
                                                <Typography variant="h3" >
                                                    {brokerID.name}
                                                </Typography>
                                            </Grid>
                                            <Grid container xs={6} justifyContent="center">
                                                <img height={50} width={50} src={brokerID.icon}></img>
                                            </Grid>
                                        </Grid> */}
                                        <FormControl fullWidth className={classes.loginInput}>
                                            <InputLabel htmlFor="public-key">Public Key</InputLabel>
                                            <OutlinedInput
                                                id="public-key"
                                                type="text"
                                                value={publics}
                                                name="public_key"
                                                onChange={handleChangePublic}
                                                label="Public Key"
                                                inputProps={{
                                                    classes: {
                                                        notchedOutline: classes.notchedOutline
                                                    }
                                                }}
                                            />
                                        </FormControl>

                                        <FormControl fullWidth className={classes.loginInput}>
                                            <InputLabel htmlFor="private-key">Private Key</InputLabel>
                                            <OutlinedInput
                                                id="private-key"
                                                type="text"
                                                value={privates}
                                                name="private_key"
                                                onChange={handleChangePrivate}
                                                label="Private Key"
                                                inputProps={{
                                                    classes: {
                                                        notchedOutline: classes.notchedOutline
                                                    }
                                                }}
                                            />
                                            {error && 
                                                <FormHelperText error id="standard-weight-helper-text-email-login">
                                                    {error}
                                                </FormHelperText>
                                            }
                                        </FormControl>

                                        <AnimateButton onClick={setSelf}>
                                            <Button
                                                onClick={setSelf}
                                                disableElevation
                                                disabled={false}
                                                fullWidth
                                                size="large"
                                                // type="submit"
                                                variant="contained"
                                                color="secondary"
                                            >
                                                Link Account
                                            </Button>
                                        </AnimateButton>

                                    </form>
                                    </Grid>
                                }
                                </Grid>
                                </MainCard>
                            </Modal>

                        </Grid>
                    </Grid>
                </MainCard>
            )}
        </React.Fragment>
    );
};

AddBrokerCard.propTypes = {
    isLoading: PropTypes.bool
};

export default AddBrokerCard;
