import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';

// material-ui
import { makeStyles, useTheme } from '@material-ui/styles';
// import { Grid, Typography, Button, Modal, Box, Card, Divider } from '@material-ui/core';
import { useSelector } from 'react-redux';
import {
    Grid,
    Box,
    Button,
    Checkbox,
    FormControl,
    FormControlLabel,
    FormHelperText,
    IconButton,
    InputAdornment,
    InputLabel,
    OutlinedInput,
    Stack,
    Typography,
    Modal,
    Card,
    Divider
} from '@material-ui/core';

import { gridSpacing } from 'store/constant';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import SubCard from 'ui-component/cards/SubCard';
import SkeletonEarningCard from 'ui-component/cards/Skeleton/EarningCard';
import configData from 'config';
import axios from 'axios';
import AnimateButton from 'ui-component/extended/AnimateButton';


// style constant
const useStyles = makeStyles((theme) => ({
    card: {
        backgroundColor: theme.palette.secondary.main,
        color: '#fff',
        overflow: 'hidden',
        position: 'relative',
    },
    cardHeading: {
        fontSize: '1.5rem',
        fontWeight: 500,
        marginRight: '8px',
        marginTop: '14px',
        marginBottom: '6px',
        color: '#fff'
    },
    subHeading: {
        fontSize: '1rem',
        fontWeight: 500,
        color: theme.palette.secondary[200]
    },
    circleIcon: {
        transform: 'rotate3d(1, 1, 1, 45deg)'
    },
    menuItem: {
        marginRight: '14px',
        fontSize: '1.25rem'
    },
    cardAction: {
        padding: '10px',
        paddingTop: 0,
        justifyContent: 'center'
    },
    primaryLight: {
        color: theme.palette.primary[200],
        cursor: 'pointer'
    },
    divider: {
        marginTop: '12px',
        marginBottom: '12px'
    },
    successDark: {
        color: theme.palette.success.dark
    },
    errorDark: {
        color: theme.palette.orange.dark
    },
    loginInput: {
        ...theme.typography.customInput
    }
}));

//===========================|| DASHBOARD DEFAULT - EARNING CARD ||===========================//

const VerifyAccountCard = () => {
    const [isLoading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [brokers, setBrokers] = useState([]);
    const [brokerID, setBrokerID] = useState(false);

    const [publics, setPublic] = useState("");
    const [privates, setPrivate] = useState("");

    const theme = useTheme();
    const classes = useStyles();
    const account = useSelector((state) => state.account);
    
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 600,
        bgcolor: 'background.paper',
      };

    const sendEmail = () => {
        axios.post(configData.API_SERVER+"users/enable", {}, {headers: { authorization: `${account.token}` }})
        .then(response => {
            console.log(response);
        })
    };

    
    return (
        <React.Fragment>
            {isLoading ? (
                <SkeletonEarningCard />
            ) : (
                <MainCard border={false} className={classes.card} contentClass={classes.content}>
                    <Grid container direction="column" justifyContent="center">
                        <Grid container item justifyContent="center">
                            <Typography className={classes.cardHeading} variant="h3">Please verify your account!</Typography>
                        </Grid>
                        <Grid item>
                            <Button
                                    className={classes.subHeading}
                                    disableElevation
                                    fullWidth
                                    size="large"
                                    type="submit"
                                    variant="contained"
                                    color="secondary"
                                    onClick={() => {sendEmail()}}
                                >
                                    Didn't get an email? Resend now! â†º
                                </Button>
                        </Grid>
                    </Grid>
                </MainCard>
            )}
        </React.Fragment>
    );
};

VerifyAccountCard.propTypes = {
    isLoading: PropTypes.bool
};

export default VerifyAccountCard;
